import React from 'react';
import Forex from 'forex-component';

const divstyle = {
  fontSize: '13px'
}

const ForexPage = () => (
  <div style={divstyle}>
    <Forex />
      <div>
        <p>
          This is a minimal, first pass at the a currency board tool
          for <a href="https://boardgamegeek.com/boardgame/227605/ex">For-Ex</a>,
          a game by Tom Russel and published
          by <a href="https://hollandspiele.com/">Hollandspiele</a>.
          There will be improvements to come.
        </p>
      </div>
  </div>);

export default ForexPage;



